.c_hamburgerMenu {
    position: fixed;
    width: 300px;
    height: 100%;
    right: -300px;
    background-color: #fff;
    transition: all 0.4s;
    // transition-timing-function: ease-in-out;
    &.open {
      transform: translateX(-300px);
    }
  }
  
  .e_hamburgerMenu_box {
      padding-left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      height: 100%;
      a {
        color: black;
        padding: 10px 0;
        text-decoration: none;
      }
  }
  
  .e_hamurerMenu_button {
    position: absolute;
    top: 0;
    right: 300px;
    width: 60px;
    height: 60px;
    border: solid #fff 1px;
    cursor: pointer;
  }

.e_icon-t, .e_icon-m, .e_icon-b {
  caret-color: transparent;
}
  
  .e_icon-t {
    width: 35px;
    height: 1px;
    background-color: #fff;
    position: absolute;
    top: 14.5px;
    left: 12.5px;
    transition: all 0.5s;
    // transition-timing-function: ease-in-out;
    &.open {
      transform: translateY(14.5px) rotate(45deg);
    }
  }
  
  .e_icon-m {
    content: '';
    width: 35px;
    height: 1px;
    background-color: #fff;
    position: absolute;
    top: 29.5px;
    left: 12.5px;
    transition: all 0.5s;
    // transition-timing-function: ease-in-out;
    &.open {
      opacity: 0;
    }
  }
  
  .e_icon-b {
    content: '';
    width: 35px;
    height: 1px;
    background-color: #fff;
    position: absolute;
    top: 44.5px;
    left: 12.5px;
    transition: all 0.5s;
    // transition-timing-function: ease-in-out;
    &.open {
      transform: translateY(-14.5px) rotate(-45deg);
    }
  }